.storyContainer {
	.unknownBlock {
		padding-left: 52px;
		p {
			width: 564px;
			max-width: calc(100% - 20px);
			margin-bottom: 24px;
		}
		p:nth-last-child(even) {
			margin-bottom: 0px;
		}
		.div {
			color: var(--darkGray);
			width: 100px;
			height: 100px;
			background-color: var(--darkGray);
		}
	}
  .img {
		width: 100%;
  }
}






















